.field {
	width: 255px;
	border: 1px solid $white;
	input {
		display: block;
		width: 100%;
		@include trans;
		@include rob;
		color: $white;
		font-size: 18px;
		font-weight: 300;
		background: rgba($black2, 0.6);
		padding: 10px 18px;
		@include placeholder {
			color: $white; } }
	&--white {
		border-color: $black;
		input {
			background: $white;
			color: $black;
			@include placeholder {
				color: $black; } } }
	&.error-field {
			border-color: red; } }
