.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 200002;
}

@media only screen and (max-width: 767px) {
  .popup {
    overflow-y: scroll;
  }
}

.popup--form .popup__content {
  width: 900px;
}

.popup__overlay {
  min-height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

@media only screen and (max-width: 599px) {
  .popup__overlay {
    display: none;
  }
}

.popup__content {
  padding: 30px 20px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  background-color: white;
  text-align: center;
}

