    position: relative;
    top: 0;
    left: 0;
    transform: translate(0, 0);
    padding: 100px 20px 50px;
  }
}

.popup__content p strong {
  display: block;
  margin-bottom: 10px;
}

.popup__close {
  position: absolute;
  width: 40px;
  height: 40px;
  top: 0;
  right: -40px;
  cursor: pointer;
  z-index: 1;
  transition: all 0.2s ease;
  padding-top: 3px;
  font-size: 20px;
}

@media only screen and (max-width: 767px) {
  .popup__close {
    right: 0;
    width: 40px;
    height: 40px;
  }
}

@media only screen and (max-width: 599px) {
  .popup__close {
    top: 15px;
    right: 50px;
  }
}

